.container {
  width: 90%;
  margin: 0 auto;
  padding: 30px 0 60px 0;
}
.section {
  width: 90%;
  margin: auto;
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}
.projectCard {
  width: 24%;
  padding-bottom: 15px;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 4px 12px;
  overflow: hidden;
  border-radius: 15px;
  background-color: #fff;
}
.projectCard img {
  width: 100%;
  transition: all 0.3s;
}
.projectCard img:hover {
  transform: scale(1.07);
}
.projectCard a {
  display: block;
  margin-top: 15px;
  text-align: center;
}
@media (max-width: 960px) {
  .container {
    width: 100%;
    padding: 40px 10px;
  }
  .section {
    width: 100%;
  }
  .projectCard a h2 {
    font-size: 28px;
  }
  .projectCard {
    width: 40%;
  }
}

@media (max-width: 768px) {
  .section {
    justify-content: center;
  }
  .projectCard {
    width: 80%;
  }
  .projectCard a h2 {
    font-size: 22px;
  }
}
