.nav {
  box-shadow: rgba(17, 17, 26, 0.05) 0px 4px 16px,
    rgba(17, 17, 26, 0.05) 0px 8px 32px;
}
.container {
  width: 90%;
  margin: 0 auto;
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo {
  width: 170px;
}
.logo img {
  max-width: 100%;
}
.ul {
  list-style: none;
  display: flex;
}
.li {
  margin-left: 20px;
}
.li a {
  font-size: 22px;
  font-weight: bold;
}
@media (max-width: 960px) {
  .li {
    margin-left: 8px;
  }
  .li a {
    font-size: 16px;
  }
  h1 {
    font-size: 32px;
  }
  .logo {
    width: 60px;
  }
  .logo img {
    width: 100%;
  }
}
