.container {
  width: 90%;
  margin: 0 auto;
  padding: 30px 0 60px 0;
}
.section {
  width: 80%;
  margin: auto;
}
.section .img {
  width: 600px;
  margin: auto;
  margin-bottom: 30px;
}
.section .img img {
  width: 100%;
}
@media (max-width: 960px) {
  .container {
    width: 100%;
    padding: 40px 10px;
  }
  .section {
    width: 100%;
  }
  .section .img {
    width: 300px;
  }
  .body p {
    font-size: 16px;
  }
}
