.container {
  width: 90%;
  margin: 0 auto;
  padding: 30px 0 0 0;
}
.section {
  width: 60%;
  margin: auto;
}
.hero {
  display: flex;
  align-items: center;
}
.hero img {
  max-width: 250px;
}
.name {
  margin-left: 30px;
}
.name h2 {
  font-size: 36px;
}
.aboutMe {
  padding: 15px 0;
  text-align: justify;
}
.aboutMe span {
  font-weight: bold;
}
.contacts button {
  background: none;
  user-select: none;
  border: none;
  margin-right: 10px;
}
.contacts button a {
  display: block;
  width: 40px;
  transition: all 0.2s;
}
.contacts button img {
  width: 100%;
}
.contacts button a:hover {
  transform: scale(1.2);
}
@media (max-width: 960px) {
  .container {
    width: 100%;
    padding: 40px 10px;
  }
  .section {
    width: 100%;
  }
  .hero {
    flex-direction: column;
  }
  .contacts {
    display: flex;
    justify-content: center;
  }
  .hero img {
    max-width: 170px;
  }
  .name {
    margin: 0;
    text-align: left;
    display: flex;
    margin-left: 0;
  }
  .name h2 {
    font-size: 28px;
  }
  .aboutMe {
    padding: 15px 0;
    text-align: center;
    font-size: 16px;
  }
}
