@import url("https://fonts.googleapis.com/css2?family=Source+Serif+4:ital,opsz,wght@0,8..60,200..900;1,8..60,200..900&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Source Serif 4", serif;
  color: #181c14;
  background-position: bottom;
  background-size: contain;
  background-repeat: no-repeat;
}
p {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.5;
}
a {
  text-decoration: none;
  color: #181c14;
}
h1 {
  font-size: 48px;
  margin-bottom: 15px;
}
h2 {
  font-size: 24px;
  font-weight: bold;
}
/* width */
::-webkit-scrollbar {
  width: 5px;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #181c14;
  border: 1px solid #181c14;
}
