.container {
  width: 90%;
  margin: 0 auto;
  padding: 30px 0 60px 0;
}
.section {
  width: 90%;
  margin: auto;
}
.articleholder {
  min-height: 250px;
  display: flex;
  align-items: center;
  gap: 20px;
  border-bottom: 1px solid #eee;
  margin-bottom: 30px;
}
.articleholder .img {
  width: 160px;
  height: 107px;
}
.img img {
  width: 160px;
  height: 107px;
}
.info h2 {
  font-size: 24px;
  font-weight: 700;
  color: #242424;
}
.info > p {
  color: #6b6b6b;
  font-family: 16px !important;
  font-weight: 400;
}

@media (max-width: 960px) {
  .container {
    width: 100%;
    padding: 40px 10px;
  }
  .section {
    width: 100%;
  }
  .info h2 {
    font-size: 20px;
  }
  .articleholder {
    flex-direction: column;
  }
  .info {
    text-align: center;
  }
}
