.container {
    width: 90%;
    margin: 0 auto;
    padding: 30px 0 60px 0;
  }
  .section {
    width: 80%;
    margin: auto;
  }
  .section .img {
    width: 600px;
    margin: auto;
    margin-bottom: 30px;
  }
  .section .img img {
    width: 100%;
  }
  .body h2 {
    text-transform: capitalize;
  }
  .body ul {
    list-style: inside;
  }
  .section a {
    display: block;
    margin: auto;
    background-color: #181c14;
    color: #eee;
    width: fit-content;
    padding: 7px 15px;
    border-radius: 7px;
  }
  @media (max-width: 960px) {
    .container {
      width: 100%;
      padding: 40px 10px;
    }
    .section {
      width: 100%;
    }
    .section .img {
      width: 300px;
    }
    .body p {
      font-size: 16px;
    }
  }
  